<template>
    <div class="applink">
      <a v-if="isExternalLink" :href="to" target="_blank" rel="noopener noreferrer">
        <slot></slot>
      </a>
      <router-link v-else :to="to">
        <slot></slot>
      </router-link>
    </div>
  </template>
  
  <script>
  export default {
    name: "AppLink",
    props: {
      to: {
        type: String,
        required: true,
      },
    },
    computed: {
      isExternalLink() {
        return /^https?:\/\//.test(this.to); // Check if 'to' starts with http or https
      },
    },
  };
  </script>
  
  <style scoped>
 
  </style>
  